body {
    text-align: center;
    margin: 0;
    box-sizing: border-box;
}


/*----- modal common ------------------------------------*/

    .modal__window {
        display: none;
        position: absolute;
        z-index: 1;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        overflow: auto;
        transition: 0.5s;
        background-color: rgb(0,0,0);  /*fall back*/
        background-color: rgba(0,0,0,0.4);
    }

    .options__window {
        display: none;
        position: absolute;
        z-index: 1;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        overflow: auto;
        transition: 0.5s;
        background-color: rgb(0,0,0);  /*fall back*/
        background-color: rgba(0,0,0,0.4);
    }

    .modal__content {
        position: relative;
        top: 50%;
        border-radius: 5px;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        background-color: #d5cabe;
        display: inline-grid;
        width: 450px;
    }

    .modal__row {
        width: 100%;
        display: -webkit-box;
        display: flex;
        flex-wrap: nowrap;
        -webkit-box-pack: center;
                justify-content: center;
        -webkit-box-align: center;
                align-items: center;
    }


/*----- modal buttons -----------------------------------*/

    .modal__btns {
        font-size: 20px;
        text-decoration: none;
        border-radius: 20px;
        border: 0;
        transition-duration: 0.2s;
        background-color: #5fc148;
        padding: 4px 30px;
    }

    .modal__btns:hover {
        background-color: #f1c138;
        border: 0;
        transition-duration: 0.2s;
    }

    .modal__btns:focus {
        outline: none;
    }

    .modal__btns--align {
        display: inline-block;
        height: 3em;
        width: 100%;
        text-align: center;
    }


/*----- start game modal --------------------------------*/

    .sg__header {
        background-color: #5fc148;
        padding: 10px 0;
        border-radius: 5px 5px 0 0;
        font-size: 1.5em;
    }

    .sg__keys_box {
        width:33%;
    }

    .sg__keys--align-rt {
        text-align: right;
    }

    .sg__keys--align-lt {
        text-align: left;
    }

    .sg__keys_box:before {
        content: "";
        display: block;
        padding-top: 10%;
    }

    .sg__graphic_box {
        width: 20%;
        min-width: 73px;
    }

    .sg__text_box {
        width: 80%;
    }

    .sg__text_box p {
        text-align: left;
        padding: 0 5px;
    }


/*----- options modal -----------------------------------*/

   .op__header {
        background-color: #5fc148;
        padding: 10px 0;
        border-radius: 5px 5px 0 0;
        font-size: 2em;
    }

    .op__text_box {
        width: 80%;

    }

    .op__char_box {
        width: 14%;
        display: -webkit-inline-box;
        display: inline-flex;
    }

    .op__level_box {
        width: 20%;
    }

    .op__choices {
        font-size: 1.5em;
        padding-top: 5px;
    }

    .op__btn--padding {
       padding-top: 10px;
    }


/*----- game over modal ---------------------------------*/

    .ov__header {
        background-color: #5fc148;
        padding: 10px 0;
        border-radius: 5px 5px 0 0;
        font-size: 2em;
    }

    .ov__graphic_box {
        width: 20%;
        min-width: 85px;
    }

    .ov__text_box {
        width: 50%;
        text-align: left;
    }

    .ov__row--padding {
       padding-bottom: 10px;
    }

    .ov__stats {
        font-size: 1.5em;
        padding-top: 5px;
    }


/*----- glyphicons --------------------------------------*/

    @font-face {
        font-family: 'Glyphicons Halflings';
        src:  url('../fonts/glyphicons-halflings-regular.eot');
        src:  url('../fonts/glyphicons-halflings-regular.eot?#iefix') format('embedded-opentype'),
              url('../fonts/glyphicons-halflings-regular.woff2') format('woff2'),
              url('../fonts/glyphicons-halflings-regular.woff') format('woff'),
              url('../fonts/glyphicons-halflings-regular.ttf') format('truetype'),
              url('../fonts/glyphicons-halflings-regular.svg#glyphicons_halflingsregular') format('svg');
    }

    .glyphicon {
        font-family: 'Glyphicons Halflings';
        font-style: normal;
        font-weight: normal;
        line-height: 1;
    }

    .glyphicon-menu-hamburger:before {content: "\e236";}
    .glyphicon-chevron-left:before {content: "\e079";}
    .glyphicon-chevron-right:before {content: "\e080";}
    .glyphicon-chevron-up:before {content: "\e113";}
    .glyphicon-chevron-down:before {content: "\e114";}
